import React, { FC, useState } from "react";
import { styled } from "@mui/material/styles";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import Collapse from "@mui/material/Collapse";
import Avatar from "@mui/material/Avatar";
import IconButton, { IconButtonProps } from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import { blue, grey, red } from "@mui/material/colors";
import FavoriteIcon from "@mui/icons-material/Favorite";
import moment from "moment";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import { Delete } from "@material-ui/icons";
import {
  arrayUnion,
  deleteDoc,
  doc,
  setDoc,
  updateDoc,
  arrayRemove,
} from "@firebase/firestore";
import { auth, firestore } from "../firebase";
import {
  CircularProgress,
  Divider,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListSubheader,
} from "@mui/material";
import CommentIcon from "@mui/icons-material/Comment";
import { CommentList } from "./CommentList";
import { TPost } from ".././types/types";
import { useAppSelector } from "../redux/hooks";
import { Feed, Icon } from "semantic-ui-react";
interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}
interface iPost {
  post: TPost;
}

const Post: FC<iPost> = ({ post }) => {
  const [expanded, setExpanded] = useState(false);
  const [loading, setLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const user = useAppSelector((data) => data.post.user);

  const likeOrDelete = post?.likes?.find(
    (d: any) => d == auth.currentUser?.uid
  );

  return (
    <>
      {/*        <ListItem style={{ width: '100%' }} alignItems='flex-start' key={post?.id}> */}
      <Feed.Event>
        <Feed.Label>
          <Avatar
            sx={{ bgcolor: grey[300] }}
            src={post?.avatar}
            aria-label="recipe"
          >
            {post?.author_nickname?.charAt(0)}
          </Avatar>
        </Feed.Label>
        <Feed.Content>
          <Feed.Summary>
            <Feed.User>{post.author_nickname}</Feed.User>
            <Feed.Date>{moment(post?.created).fromNow()}</Feed.Date>
          </Feed.Summary>
          <Feed.Extra text>{post.text}</Feed.Extra>
          {post.image_url ? (
            <Feed.Extra images>
              <a>
                <img src={post.image_url} />
              </a>
            </Feed.Extra>
          ) : null}
          <Feed.Meta>
            <IconButton
              onClick={async () => {
                if (
                  post.id !== undefined &&
                  auth.currentUser?.uid !== undefined
                ) {
                  if (likeOrDelete) {
                    await setDoc(
                      doc(firestore, `posts`, post.id),
                      {
                        likes: arrayRemove(auth.currentUser.uid),
                      },
                      { merge: true }
                    );
                  } else {
                    await setDoc(
                      doc(firestore, `posts`, post.id),
                      {
                        likes: arrayUnion(auth.currentUser.uid),
                      },
                      { merge: true }
                    );
                  }
                }
              }}
              aria-label="add to favorites"
            >
              <FavoriteIcon
                style={{ color: !likeOrDelete ? "inherit" : "red" }}
              />
            </IconButton>

            {post?.likes?.length}
            <IconButton aria-label="share" onClick={handleOpen}>
              <CommentIcon />
              {/*  <ShareIcon /> */}
            </IconButton>

            {post.totalMessages}

            <IconButton
              onClick={async () => {
                console.log(post.id, "userId");
                setLoading(true);
                if (post.user_id !== null || undefined) {
                  await deleteDoc(
                    doc(firestore, `posts/${post?.id}/comments`)
                  ).then(async () => {
                    await deleteDoc(doc(firestore, "posts", post?.id)).then(
                      () => {
                        setLoading(false);
                      }
                    );
                  });
                }
              }}
              aria-label="delete"
            >
              {loading == true ? (
                <CircularProgress />
              ) : auth?.currentUser?.uid == post?.user_id ? (
                <DeleteOutlineIcon />
              ) : null}
            </IconButton>
            <CommentList
              open={open}
              setOpen={setOpen}
              id={post?.id}
              post={post}
            />
          </Feed.Meta>

          {/*  <ListItemAvatar>
                <Avatar sx={{ bgcolor: grey[500] }} src={post?.avatar} aria-label="recipe">
                    {post?.author_nickname?.charAt(0)}
                </Avatar>
            </ListItemAvatar> */}
        </Feed.Content>
      </Feed.Event>
    </>
  );
};

export default Post;
