export const DrawerPanel = ({ visible, setVisible, open, setOpen }: any) => {
  const signOutHandler = (index: number) => {
    if (index == 2) auth.signOut();
    console.log(index, "index");
  };

  return (
    <Drawer
      variant="permanent"
      open={open}
      onClose={() => setOpen(false)}
      sx={{
        width: 240,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: { width: 240, boxSizing: "border-box" },
      }}
    >
      <Toolbar />
      <Box sx={{ overflow: "auto" }}>
        <List>
          {["HOME", "LOGIN", "EXIT"].map((text, index) => (
            <Link to={index == 0 ? "/todoapp/home/" : index == 1 ? "/todoapp/login" : "/todoapp/login"}>
              <ListItem button key={text} onClick={() => signOutHandler(index)}>
                <ListItemIcon>
                  {index == 0 ? (
                    <Home />
                  ) : index == 1 ? (
                    <LoginIcon />
                  ) : (
                    <LogoutIcon />
                  )}
                </ListItemIcon>

                <ListItemText primary={text} />
              </ListItem>
            </Link>
          ))}
        </List>
        <Divider />
        <List>
          {["PROFILE", "SETTINGS"].map((text, index) => (
            <Link
              to={
                index == 0 ? "/todoapp/profile/" : index == 1 ? "/todoapp/settings" : "/todoapp/login"
              }
            >
              <ListItem button key={text}>
                <ListItemIcon>
                  {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                </ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            </Link>
          ))}
        </List>
      </Box>
    </Drawer>

    /*    <Sidebar
           style={{ background: '#0d6fa8' }}
           as={Menu}
           animation='push'
           icon='labeled'
           direction={'left'}

           inverted
           onHide={() => setVisible(false)}
           vertical
           visible={visible}
           width='thin'
       >
           <Link to='/home/'>
               <Menu.Item as='a'>
                   <Icon name='home' />
                   Home
           </Menu.Item>
           </Link>

           <Menu.Item as='a' onClick={()=>{
               auth.signOut()
           }}>
               <Icon name='sign in' />
                   Exit
           </Menu.Item>

           <Menu.Item as='a'>
               <Icon name='conversation' />
                Channels
           </Menu.Item>
       </Sidebar> */
  );
};

export const DrawerPanelMessages = ({
  index,
  currentId,
  posts,
}: {
  index?: number;
  currentId?: number;
  posts: Array<TPost>;
}) => {
  const [text, setText] = useState<string>("");
  const [posts2, setPosts2] = useState<Array<TPost>>([]);

  useEffect(() => {
    const q = query(
      collection(firestore, "posts"),
      where("text", "==", text),
      orderBy("created", "desc"),
      limit(10)
    );

    onSnapshot(q, (data) => {
      const list: any = [];
      data.forEach((doc) => {
        console.log(doc.data().text, "doc");
        list.push({ ...doc.data(), id: doc.id });
      });
      console.log(list, "list");
      setPosts2(list);
    });
  }, [text]);

  return (
    <Drawer
      variant="permanent"
      sx={{
        width: 270,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: { width: 270, boxSizing: "border-box" },
      }}
      anchor="right"
    >
      <Toolbar />
      <Box sx={{ overflow: "auto" }}>
        <List>
          <ListItem>
            <TextField
              value={text}
              onChange={(e) => {
                setText(e.currentTarget.value);
              }}
              fullWidth={true}
              id="input-with-icon-textfield"
              label="Search..."
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Search />
                  </InputAdornment>
                ),
                endAdornment: (
                  <>
                    {text.length > 0 ? (
                      <InputAdornment position="start">
                        <IconButton
                        onClick={()=>{
                          setText('')
                        }}
                        >
                          <Clear />
                        </IconButton>
                      </InputAdornment>
                    ) : null}
                  </>
                ),
              }}
              variant="standard"
            />
          </ListItem>
          {posts2.length > 0 ? (
            <ListItem>
              <PostList posts={posts2} />
            </ListItem>
          ) : (
            <div
              style={{
                fontSize: 19,
                color: "#c6cccf",
                fontWeight: "900",
                display: "flex",
                justifyContent: "center",
              }}
            >
              type something...
            </div>
          )}
        </List>
      </Box>
    </Drawer>
  );
};
