import React, { useState, useEffect } from 'react'
import '../App.css';
/* import firebase from '../firebase' */
import 'semantic-ui-css/semantic.min.css'
/* import TodoForm from './components/form';
import TodoList from './components/TodoList'; */
import { Form, Button, Card, Image } from 'semantic-ui-react'
import { getAuth, createUserWithEmailAndPassword, signOut, signInWithEmailAndPassword } from "firebase/auth";
import { auth, firestore } from '../firebase';
import { setDoc, doc, collection, addDoc, } from 'firebase/firestore'
import { Agent } from 'http';
import { useParams, useNavigate } from 'react-router-dom'

export const Signin = () => {
  const [email, setEmail] = useState('')
  const [pass, setPass] = useState('')
  const [email2, setEmail2] = useState('')
  const [pass2, setPass2] = useState('')
  const [nickname, setNickname] = useState('')
  const [age, setAge] = useState('')
  const navigate = useNavigate()

console.log(email, )



  return (
    <div className='ui container' style={{ background: '#fff' }}>
    

      <div style={{ marginTop: 30, paddingBottom: 200, display: 'flex', justifyContent: 'center', }}>
        <Card>
          <Card.Content>
            <Card.Header>SignUp</Card.Header>

            <Form style={{ width: 300, marginTop: 30, }}>
              <Form.Group widths='equal' >
                <Form.Input value={email} onChange={(e) => {
                  setEmail(e.target.value)
                }} fluid label='email' placeholder='email' />
                <Form.Input value={nickname} onChange={(e) => {
                  setNickname(e.target.value)
                }} fluid label='nickname' placeholder='nickname' />

              </Form.Group>

              <Form.Group widths='equal'>
                <Form.Input value={age} onChange={(e) => {
                  setAge(e.target.value)
                }} fluid label='age' placeholder='age' />
                <Form.Input value={pass} onChange={(e) => {
                  setPass(e.target.value)
                }} fluid label='password' placeholder='password' />
              </Form.Group>
              <Card.Content extra>
                <div className='ui two buttons'>
                  <Button basic color='green' onClick={() => {


                    createUserWithEmailAndPassword(auth, email, pass).then(async () => {
                      if (auth.currentUser?.uid !== undefined) {
                        const id: any = auth.currentUser?.uid
                        console.log('firestore')
                        await setDoc(doc(firestore, 'users', id), {
                          email: email,
                          nickname: nickname,
                          age: age,
                        }).then(()=>{
                          navigate('/home/')
                        }).catch((err) => {
                          if (err.code == 'auth/email-already-exists') {
                            console.log('email already exists')
                          }
                        })
                      }

                    })

                  }}>submit</Button>

                </div>
              </Card.Content>
            </Form>

            <Card.Header style={{ marginTop: 30, }}>Login</Card.Header>

            <Form style={{ width: 300, marginTop: 30, }}>
              <Form.Group widths='equal' >
                <Form.Input value={email2} onChange={(e) => {
                  setEmail2(e.target.value)
                }} fluid label='email' placeholder='email' />
                <Form.Input value={pass2} onChange={(e) => {
                  setPass2(e.target.value)
                }} fluid label='password' placeholder='password' />
              </Form.Group>
              <Card.Content extra>
                <div className='ui two buttons'>
                  <Button basic color='green' onClick={() => {
                    signInWithEmailAndPassword(auth, email2, pass2).then(()=>navigate('/home/')).catch((err) => {
                      console.log(err, 'signed in error')
                    })
                  }}>
                    submit
                          </Button>
                </div>
              </Card.Content>
            </Form>

          </Card.Content>

        </Card>

      </div>

    </div>
  )
}


