import { Avatar, CircularProgress, FormControl, IconButton, Modal, Paper, Popover, TextField } from '@mui/material'
import React, { FC, useEffect, useState } from 'react'
import { Button, Card } from 'semantic-ui-react'
import PostList from './PostList'
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import PhoneIcon from '@mui/icons-material/Phone';
import FavoriteIcon from '@mui/icons-material/Favorite';
import PersonPinIcon from '@mui/icons-material/PersonPin';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import TextsmsOutlinedIcon from '@mui/icons-material/TextsmsOutlined';
import { auth, firestore } from '../firebase';
import { FormPost } from './FormPost';
import { collection, doc, getDoc, limit, onSnapshot, orderBy, query, updateDoc, where } from '@firebase/firestore';
import { Clear } from '@material-ui/icons';
import { ProfileEdit } from './ProfileEdit';
import { useSelector } from 'react-redux';
import { useAppSelector } from '../redux/hooks';
import styles from '../styles/profile.module.css'

interface TabPanelProps {
    children?: React.ReactNode;
    index: number;
    value: number;
}

function TabPanel(props: TabPanelProps) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 2, }}>
                    <div>{children}</div>
                </Box>
            )}
        </div>
    );
}
interface iPosts {
    text: string,
    id: string
}

interface IProfile {
    posts: Array<iPosts>
}
export const Profile: FC<IProfile> = ({ posts }) => {
    const [value, setValue] = useState(0);
    const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
    const [edit, setEdit] = useState(false)
    const handleChange = (event: React.SyntheticEvent, newValue: number) => {
        setValue(newValue);
    };
    const [openCreatePost, setOpenCreatePost] = useState(false)
    const open = Boolean(anchorEl);
    const [userPosts, setUserPosts] = useState([])
    const user = useAppSelector(data => data.post.user)


    const [image, setImage]: any = useState(null)
    useEffect(() => {
        if (auth?.currentUser?.uid !== undefined) {
            const q = query(collection(firestore, 'posts'), where('user_id', '==', auth.currentUser?.uid), orderBy('created', 'desc'), limit(10))

            onSnapshot(q, (data) => {

                const list: any = []
                data.forEach((doc) => {

                    console.log(doc.data().text, 'doc')
                    list.push({ ...doc.data(), id: doc.id })
                })
                setUserPosts(list)
            })
        }
    }, [])


    return (

        <div className={styles.profile}>
            <div className={styles.first}>
                {
                    image !== null ?
                        <Avatar style={{ width: 200, height: 200, }} alt='imagePost' src={image !== null && image !== undefined ? URL.createObjectURL(image !== undefined || null ? image : '') : ''} />
                        : <Avatar
                            alt='userAvatar'
                            src={user?.avatar}
                            sx={{ width: 200, height: 200, }}
                        >
                            {user.nickname?.charAt(0)}
                    </Avatar>
                }


                <ProfileEdit setEdit={setEdit} edit={edit} image={image} setImage={setImage} />
                <Button onClick={() => {
                    setEdit(true)
                }} style={{ width: '100%', maxWidth: 200, marginTop: 10 }}>{edit == true ? 'cancel' : 'Edit profile'}</Button>
            </div>


            <div className={styles.second}>

                <Tabs
                    indicatorColor='primary'
                    variant='fullWidth'
                    textColor="inherit"
                    value={value} onChange={handleChange} aria-label="icon label tabs example">
                    <Tab style={{ color: '#585859' }} icon={<TextsmsOutlinedIcon />} label="POSTS" />
                    <Tab style={{ color: '#585859' }} icon={<SettingsOutlinedIcon />} label="SETTINGS" />
                    <Tab style={{ color: '#585859' }} icon={<PersonPinIcon />} label="NEARBY" />

                </Tabs>

                <TabPanel value={value} index={0}>
                    <Button
                        fluid={true}
                        onClick={(e) => {
                            setOpenCreatePost(true)
                        }}
                    >create new post</Button>
                    <Modal
                        open={openCreatePost}
                        onClose={() => setOpenCreatePost(false)}
                    >

                        <Paper
                            sx={{
                                position: 'absolute',
                                top: '50%',
                                left: '50%',
                                transform: 'translate(-50%, -50%)',
                                width: 'auto',
                                bgcolor: 'background.paper',
                                boxShadow: 24,
                                p: 4,
                            }}>
                            <FormPost setOpenCreatePost={setOpenCreatePost} />
                        </Paper>
                        
                    </Modal>

                    <PostList posts={userPosts} />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    Item Two
                    </TabPanel>
                <TabPanel value={value} index={2}>
                    Item Three
                    </TabPanel>
            </div>

        </div>
    )
}