import React, { useState, useEffect, Fragment } from 'react'
import './App.css';
import { auth, db, firestore } from './firebase'
import { getDatabase, ref, onValue } from "firebase/database";
import 'semantic-ui-css/semantic.min.css'
import TodoForm from './components/form';
import PostList from './components/PostList';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Badge from '@mui/material/Badge';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@mui/icons-material/Mail';
import NotificationsIcon from '@mui/icons-material/Notifications';
import MoreIcon from '@mui/icons-material/MoreVert';
import { Signin } from './components/Signin';
import { DrawerPanel, DrawerPanelMessages } from './components/Drawer'
import { Profile } from './components/profile';
import { collection, doc, getDoc, limit, onSnapshot, orderBy, query } from '@firebase/firestore';
import { setupMaster } from 'cluster';
import { useDispatch } from 'react-redux';
import { postData } from './redux/post';
import { useAppDispatch } from './redux/hooks'
import { Sidebar } from 'semantic-ui-react';
import { Container } from '@mui/material';
import { Home } from '@mui/icons-material';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
}));



function App() {
  const [currentId, setCurrentId] = useState<number>(3)
  const [index, setIndex] = useState<number>(0)

  const [todoList, setTodoList] = useState([])
  const [posts, setPosts] = useState([])
  const [visible, setVisible] = useState<boolean>(false)
  const [user, setUser] = useState([])
  const navigate = useNavigate()
  const dispatch = useDispatch()

  useEffect(() => {

    const q = query(collection(firestore, 'posts'), orderBy('created', 'desc'), limit(10))

    onSnapshot(q, (data) => {

      const list: any = []
      data.forEach((doc) => {
        console.log(doc.data().text, 'doc')
        list.push({ ...doc.data(), id: doc.id })
      })
      console.log(list, 'list')
      setPosts(list)
    })
    const fetchUser = async () => {
      if (auth.currentUser?.uid !== undefined) {

        const id = auth.currentUser?.uid
        const userDoc = doc(firestore, 'users', id)

        const userSnap: any = await getDoc(userDoc)
        console.log(userSnap.data(), 'usersnap')
        if (userSnap.exists()) {
          dispatch(postData(userSnap.data()))
        }
      }
    }

    fetchUser()


  }, [auth.currentUser?.uid])
  console.log(auth.currentUser?.uid, 'id')

  useEffect(() => {

    if (auth.currentUser !== null) {
      navigate('/todoapp/home')
    } else {
      navigate('/todoapp/login')
    }

  }, [auth.currentUser])
  const [anchorEl, setAnchorEl] = useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event: any) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event: any) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const menuId = 'primary-search-account-menu';


  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      
      <MenuItem>
        <IconButton size="large" aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={4} color="error">
            <MailIcon />
          </Badge>
        </IconButton>
        <p>messages</p>
      </MenuItem>
      <MenuItem>
        <IconButton
          size="large"
          aria-label="show 17 new notifications"
          color="inherit"
        >
          <Badge badgeContent={17} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p>notification</p>
      </MenuItem>
      <Link to='/profile'>
        <MenuItem >
          <IconButton
            size="large"
            aria-label="account of current user"
            aria-controls="primary-search-account-menu"
            aria-haspopup="true"
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <p>profile</p>
        </MenuItem>
      </Link>
    </Menu>
  );

  return (
  
    <div style={{ background: '#fff' }}>
     

        <Box sx={{ flexGrow: 1 }}>
          <AppBar sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }} style={{}} position="fixed">
            <Toolbar>
              <IconButton
                size="large"
                edge="start"
                color="inherit"

                onClick={(): any => {
                  setVisible(true)
                }}
                sx={{ mr: 10 }}
              >
                <MenuIcon />
              </IconButton>
              <Typography
                variant="h6"
                noWrap
                component="div"
                sx={{ display: { xs: 'none', sm: 'block' } }}
              >
                {/* firebase.auth().currentUser?.email ? firebase.auth().currentUser?.email :  */'User'}
              </Typography>
              <Search>
                <SearchIconWrapper>
                  <SearchIcon />
                </SearchIconWrapper>
                <StyledInputBase
                  placeholder="Search"
                  inputProps={{ 'aria-label': 'search' }}
                />
              </Search>
              <Box sx={{ flexGrow: 1 }} />
              <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
                <IconButton size="large" aria-label="show 4 new mails" color="inherit">
                  <Badge badgeContent={4} color="error">
                    <MailIcon />
                  </Badge>
                </IconButton>
                <IconButton
                  size="large"
                  aria-label="show 17 new notifications"
                  color="secondary"
                >
                  <Badge badgeContent={17} color="error">
                    <NotificationsIcon />
                  </Badge>
                </IconButton>
                <Link to='/profile'>
                  <IconButton
                    size="large"
                    edge="end"
                    aria-label="account of current user"
                    aria-controls={menuId}
                    aria-haspopup="true"
                    onClick={handleProfileMenuOpen}
                    color="inherit"
                  >
                    <AccountCircle style={{ color: '#fff' }} />
                  </IconButton>
                </Link>

              </Box>
              <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
                <IconButton
                  size="large"
                  aria-label="show more"
                  aria-controls={mobileMenuId}
                  aria-haspopup="true"
                  onClick={handleMobileMenuOpen}
                  color="inherit"
                >
                  <MoreIcon />
                </IconButton>
              </Box>
            </Toolbar>
          </AppBar>
          {renderMobileMenu}

        </Box>

        <DrawerPanel visible={visible} setVisible={setVisible} />
        <DrawerPanelMessages posts={posts} currentId={currentId} index={index} />

        <Toolbar/>
       <Box component='main' sx={{flexGrow: 1, p: 3,}}>
          <Routes>
            <Route
              path='/todoapp/home/'
              element={
                <div style={{ paddingBottom: 30, textAlign: 'center', height: '100vh', marginTop: 10, overflow: 'auto', display: 'table', tableLayout: 'fixed', width: '100%' }}>
                    <PostList posts={posts} currentId={currentId} index={index} />

                </div>
              }
            />

            <Route
              element={<Signin />}
              path='/todoapp/login'
            />
            <Route
              element={<Profile posts={posts} />}
              path='/todoapp/profile'
            />
          </Routes>
     

          </Box>
      

    
    </div>
      
  )
}

export default App;
